::-webkit-scrollbar {
  width: 0;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

main {
  position: relative;
  display: flex;
  flex-direction: column;
  flex: 1 0 auto;
}

body {
  position: relative;
  display: flex;
  flex-direction: column;
  padding-bottom: 100px;
  min-height: 100%;
  overflow-x: hidden;
  font-family: var(--font-family);
  background-color: #fff;

  &.stop {
    overflow: hidden;
  }
}

img {
  max-width: 100%;
  pointer-events: none;
  object-fit: cover;
}

a {
  color: #000;
  transition: all 0.3s ease-in-out;

  & svg {
    transition: all 0.3s ease-in-out;
  }
}

button {
  font-family: var(--font-family);
  transition: all 0.3s ease-in-out;

  & svg {
    transition: all 0.3s ease-in-out;
  }
}

input {

  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button {
    -webkit-appearance: none;
    -moz-appearance: textfield;
  }


  &.error {
    color: var(--ff3d3d) !important;

    &::-webkit-input-placeholder {
      color: var(--ff3d3d) !important;
    }

    &::-moz-placeholder {
      color: var(--ff3d3d) !important;
    }

    &:-ms-input-placeholder {
      color: var(--ff3d3d) !important;
    }

    &::-ms-input-placeholder {
      color: var(--ff3d3d) !important;
    }

    &::placeholder {
      color: var(--ff3d3d) !important;
    }
  }
}

textarea {

  &.error {
    color: var(--ff3d3d) !important;

    &::-webkit-input-placeholder {
      color: var(--ff3d3d) !important;
    }

    &::-moz-placeholder {
      color: var(--ff3d3d) !important;
    }

    &:-ms-input-placeholder {
      color: var(--ff3d3d) !important;
    }

    &::-ms-input-placeholder {
      color: var(--ff3d3d) !important;
    }

    &::placeholder {
      color: var(--ff3d3d) !important;
    }
  }
}

.label-global {
  display: block;

  &__input {
    width: 100%;
    padding: 12px 16px;
    border: 0.49px solid rgba(0, 0, 0, 0.4);
    border-radius: 8px;

    font-weight: 400;
    font-size: 16px;
    color: #000;

    transition: all 0.3s ease-in-out;

    &::placeholder {
      font-weight: 700;
      font-size: 16px;
      color: #000;
      opacity: 0.4;
    }

    &:hover {
      background-color: #e3e3e3;
    }

    &:focus {
      background-color: #e3e3e3;
    }
  }

  &.active {

    & .label-global__input {
      background-color: #e3e3e3;

      &::placeholder {
        color: #000;
        opacity: 1;
      }
    }
  }
}

.formSelect {
  position: relative;

  &-btn {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 14px 20px 14px 11px;

    font-weight: 700;
    font-size: 16px;
    color: #000;

    border: 0.49px solid rgba(0, 0, 0, 0.4);
    border-radius: 8px;
    background-color: #fff;
    z-index: 2;

    & svg {
      stroke: #929393;
      transition: all 0.3s ease-in-out;
    }

    & p {
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 1;
      text-overflow: ellipsis;
      overflow: hidden;
      max-width: 100%;
      text-align: start;
    }

    &.error {
      color: var(--ff3d3d);
    }

    &:hover {
      background-color: #e3e3e3;

      & svg {
        stroke: #000;
      }
    }
  }

  &-list {
    position: absolute;
    top: 0;
    left: 0;

    display: flex;
    flex-direction: column;
    width: 100%;
    max-height: 220px;
    overflow-y: auto;
    border-radius: 15px;

    background-color: #fff;
    border: 0.49px solid rgba(0, 0, 0, 0.4);
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
    z-index: 1;

    transition: all 0.3s ease-in-out;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;

    &__item {
      position: relative;
      padding: 12px 10px;
      transition: all 0.3s ease-in-out;
      cursor: pointer;

      &:hover {
        color: #fd8a00;
      }

      &-descr {
        font-weight: 700;
        font-size: 16px;
        color: #000;
        background-color: #fff;
        transition: all 0.3s ease-in-out;
        cursor: pointer;
      }

      & input {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        cursor: pointer;

        opacity: 0;
      }

      &:hover {
        background-color: #e3e3e3;
      }
    }
  }

  &.active {
    z-index: 3;

    & .formSelect-btn {

      & svg {
        transform: rotate(180deg);
      }
    }

    & .formSelect-list {
      opacity: 1;
      visibility: visible;
      pointer-events: all;
    }
  }

  &.error {

    & .formSelect-btn {
      color: var(--ff3d3d);
    }
  }
}

.btn-global {
  display: flex;
  align-items: center;
  justify-content: center;

  padding: 13px 10px;
  border-radius: 6px;

  font-weight: 400;
  font-size: 16px;
  line-height: 100%;
  text-align: center;
  color: #fff;

  &:hover {
    opacity: 0.7;
  }

  &.active {
    background-color: var(--fd8a00);
  }

  &.disabled {
    pointer-events: none;
  }
}

@media (max-width: 767px) {
  .btn-global {
    font-size: 12px;
  }

  .label-global {

    &__input {
      font-size: 12px;

      &::placeholder {
        font-size: 12px;
      }
    }
  }

  .formSelect {
  position: relative;

  &-btn {
    font-size: 12px;
  }

  &-list {

    &__item {

      &-descr {
        font-size: 12px;
      }
    }
  }
}
}
