}

.mobile-menu {
  display: none;
  position: fixed;
  left: 0;
  bottom: 16px;
  width: 100%;
  padding: 0 16px;

  &__content {
    position: relative;
    padding: 22px 32px;

    border-radius: 14px;
    background-color: #000;
    transition: all 0.3s ease-in-out;
    z-index: 50;

    &::after {
      content: '';
      position: absolute;
      top: 1px;
      left: 50%;
      transform: translateX(-50%);
      width: calc(100% - 33px);
      height: 1px;
      background-color: #fff;
      opacity: 0;

      transition: all 0.3s ease-in-out;
    }

    &-expanded {
      position: absolute;
      bottom: 50%;
      left: 0;
      width: 100%;
      max-height: 50vh;
      overflow-y: auto;

      border-radius: 14px 14px 0 0;
      background-color: #000;

      opacity: 0;
      visibility: hidden;
      pointer-events: none;
      transition: all 0.3s ease-in-out;

      z-index: 49;

      & .expanded__list {
        display: flex;
        flex-direction: column;
        gap: 20px;
        padding: 23px 24px 25px 24px;

        &-item {

          &__link {
            line-height: 100%;
            font-weight: 400;
            font-size: 20px;
            color: #fff;
          }
        }
      }
    }

    &-main {

      & .main__menu {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 10px;

        &-item {

          &__link {

            & svg {
              fill: #7f7f7f;
            }

            &.active {

              & svg {
              fill: #fff;
            }
            }
          }
        }
      }
    }
  }
  &.active {

    & .mobile-menu__content {
      border-radius: 0 0 14px 14px;

      &::after {
        opacity: 1;
      }

      &-expanded {
        bottom: 99%;

        opacity: 1;
        visibility: visible;
        pointer-events: all;
      }

      &-main {

        & .main__menu {

          &-item {

            &__link {

              &:last-child {
                transform: rotate(-90deg);
              }
            }
          }
        }
      }
    }
  }
}
