                  }

                  &.date {
                    font-size: 8px;
                  }

                  &.value {
                    gap: 0;
                    font-size: 10px;

                    &::after {
                      content: 'p';
                      width: unset;
                      height: unset;
                      background-color: inherit;

                      font-weight: 700;
                      font-size: 10px;
                      color: #000;
                    }
                  }
                }

                &.balance {
                  margin-left: 13px;
                }
              }

              &-choice {
                gap: 6px;

                & .type {
                  font-size: 8px;
                }
              }
            }
          }
        }
      }
    }
  }
  .admin-balance {
    padding-top: 68px;

    & .container {
      max-width: 100%;
    }

    &__content {

      &-top {
        padding-left: 4px;
        margin-bottom: 28px;

        &__title {

          & * {
            font-size: 16px;
          }
        }
      }

      &-info {
        display: flex;
        flex-direction: column;
        gap: 5px;
        margin-bottom: 21px;
        background-color: inherit;

        &__btn {
          position: relative;
          justify-content: start;
          padding: 18px 12px;
          background-color: #f3f4f5;
          color: #fff;

          & p {
            position: relative;
            z-index: 2;
          }

          &.active {
            background-color: #f3f4f5;
          }

          & .icon {
            display: none;
          }

          &::after {
            content: "p";
            z-index: 2;
          }

          &::before {
            content: '';
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: calc(100% - 8px);
            height: calc(100% - 8px);
            border-radius: 8px;
            background-color: #000;
            z-index: 1;
          }
        }
      }

      &-form {

        &__top {
          gap: 21px;

          &-users {
            gap: 8px;
          }
        }

        & .admin-balance__form {
          gap: 15px;

          & .formSelect {

            &-btn {
              padding: 17px 20px 16px 11px;
            }
          }

          & .label-global {

            &__input {
              padding: 16px 10px;
            }
          }

          &-submit {
            padding: 18px 10px;
          }
        }
      }
    }
  }
