.header {
  position: fixed;
  top: 0;
  width: 100%;
  padding: 29px 0;
  background-color: #f8f8f8;
  z-index: 40;

  &.header-user {
    padding: 20px 0;
  }

  &__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 20px;
    padding: 0 40px;

    &-left {
      display: flex;
      align-items: center;

      &__logo {

        &-link {
          font-weight: 700;
          font-size: 18px;
          text-transform: uppercase;
          color: var(--ed1c24);
        }
      }

      &__info {
        display: flex;
        align-items: center;
        margin-left: 38px;
        gap: 28px;

        &-name {
          font-weight: 700;
          font-size: 18px;
          text-transform: uppercase;
          color: #000;
        }

        &-balance {
          display: flex;
          align-items: center;
          gap: 8px;

          font-weight: 400;
          font-size: 18px;
          color: #000;

          & .value {
            font-weight: 700;
            font-size: 32px;
          }

          & .icon {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            background-color: #ff4b00;

            font-weight: 400;
            font-size: 32px;
            text-align: center;
            color: #fff;
          }
        }
      }
    }

    &-right {
      display: flex;
      align-items: center;
      gap: 49px;

      &__nav {

        & .nav__list {
          display: flex;
          align-items: center;
          gap: 20px;

          &-item {

            &__link {
              white-space: nowrap;
              font-family: var(--second-family);
              font-weight: 500;
              font-size: 16px;
              line-height: 100%;
              letter-spacing: -0.02em;
              color: var(--151515);

              &:hover {
                color: var(--ed1c24);
              }
            }
          }
        }
      }

      &__social {

        & .social__list {
          display: flex;
          align-items: center;
          gap: 18px;

          &-item {

            &:first-child {
              margin-right: 12px;
            }

            &__link {
              font-family: var(--font3);
              font-weight: 600;
              font-size: 12px;
              text-transform: uppercase;
              color: var(--07171d);

              &:hover {
                color: var(--ed1c24);
              }
            }
          }
        }
      }
    }
  }
}

@media (max-width: 1365px) {
  .header {

    &__content {
      flex-direction: column;
      align-items: unset;

      &-left {

        &__info {
          margin-left: auto;
        }
      }

      &-right {
        flex-direction: column;
        gap: 21px;
      }
    }
  }
}

@media (max-width: 767px) {

  .header {
    display: none;
  }
}
